import React, {Component} from "react";

// game engine
import Player from "../engine/player";
import Farm from "../engine/farm";

// game
import PlayerComponent from "./Player"
import FarmComponent from "./Farm";
import ItemsComponent from "./Items";
import CraeftersComponent from "./Craefters";
import Weapon from "../engine/items/weapon";
import Resources from "../engine/resources";
import {ResourceTypes} from "../engine/data/types";

const version = "v0.1.0";
const versionMsg = `Welcome to Cräft! version: ${version}`;

/* eslint-disable-next-line no-console */
console.log(versionMsg);

global.delay = 2;

export default class Craeft extends Component {

    gameTick = null;

    state = {
        logs: [
            versionMsg
        ],
        // the player
        player: new Player(),
        // the farm
        farm: new Farm(),
        // craefters
        craefters: [],
        // items
        items: [],
        // resources
        resources: new Resources({initialResources: 100})
    };

    constructor(props) {
        super(props);

        this.farmComplete = this.farmComplete.bind(this);
        this.farmStart = this.farmStart.bind(this);

        this.addCraefter = this.addCraefter.bind(this);
        this.addItem = this.addItem.bind(this);

        this.equipItem = this.equipItem.bind(this);
        this.unEquipItem = this.unEquipItem.bind(this);

        const knife = new Weapon({
            name: "Newbie Knife",
            material: ResourceTypes.Metal,
            atk: 1,
            matk: 1,
            delay: -1
        });

        this.state.player.equipment.equip(knife);

        knife.equiped = true;

        this.state.items = [
            knife
        ];
    }

    componentDidMount() {
        // re-render every second
        this.gameTick = setInterval(() => {
            // tick the player
            this.state.player.tick();

            // tick all craefters
            for (const craefter of this.state.craefters) {
                craefter.tick();
            }

            // todo: tick the items

            // force update of the UI
            this.forceUpdate();
        }, 1 * 1000);
    }

    componentWillUnmount() {
        clearInterval(this.gameTick)
    }

    farmStart(
        sta
    ) {
        this.state.player.exhaust(sta)
    }

    farmComplete(
        result
    ) {
        const resources = new Resources()
            .add(this.state.resources)
            .add(result);

        this.state.player.damage(10);
        this.state.player.addExp(10);

        this.setState({
            resources
        })
    }

    addCraefter(
        craefter
    ) {
        const craefters = [...this.state.craefters];

        craefters.push(craefter);

        craefter.onDoneCreating = () => {
            this.state.player.addExp(5);
        };

        this.setState({
            craefters
        })
    }

    addItem(
        item,
        resourcesConsumed,
        craefter
    ) {
        const items = [...this.state.items];
        const resources = new Resources()
            .add(this.state.resources)
            .sub(resourcesConsumed);

        item.onDoneCreating = () => {
            this.log(`${item.name} cräfted! `);
            craefter.addExp(5);
        };

        items.push(item);

        this.setState({
            items,
            resources
        })
    }

    equipItem(
        item
    ) {
        const equiped = this.state.player.equipment.equip(item);

        if (equiped) {
            this.log(`${item.name} put on.`);

            item.equiped = equiped;

            let items = [...this.state.items];

            this.setState({
                items
            })
        } else {
            this.log("Equip failed!")
        }
    }

    unEquipItem(
        item
    ) {
        const unequiped = this.state.player.equipment.unequip(item);

        if (unequiped) {

            this.log(`${item.name} taken off.`);

            let items = [...this.state.items];
            items[items.indexOf(item)].equiped = false;

            this.setState({
                items
            })
        } else {
            this.log("Unequip failed!")
        }
    }

    log(
        entry
    ) {
        const logs = [...this.state.logs];

        logs.push(entry);

        this.setState({
            logs
        })
    }

    render() {
        return (
            <div className={`craeft${this.state.player.dead ? "rpgui-disabled" : ""}`}>


                <PlayerComponent player={this.state.player}
                                 onUnequip={this.unEquipItem}
                                 logs={this.state.logs}/>

                <div className="craefting-interface columns">

                    <CraeftersComponent resources={this.state.resources}
                                        craefters={this.state.craefters}
                                        craefterAdded={this.addCraefter}
                                        itemAdded={this.addItem}/>

                    <FarmComponent resources={this.state.resources}
                                   farm={this.state.farm}
                                   player={this.state.player}
                                   farmStart={this.farmStart}
                                   farmComplete={this.farmComplete}/>

                    <ItemsComponent items={this.state.items}
                                    onItemEquip={this.equipItem}/>

                </div>

            </div>
        );
    }
}
