import React, {Component} from "react";
import PropTypes from "prop-types";
import {ResourceTypes} from "../engine/data/types";

export default class Farm extends Component {

    static propTypes = {
        player: PropTypes.object,
        resources: PropTypes.object,
        farm: PropTypes.object,
        farmComplete: PropTypes.func,
        farmStart: PropTypes.func
    };

    state = {
        isFarming: false
    };

    constructor(props) {
        super(props);

        this.farm = this.farm.bind(this)
    }

    farm() {
        if (!this.state.isFarming && this.props.player.staCurrent > 0) {

            this.setState({
                    isFarming: true
                },
                () => {
                    this.props.farmStart(1);
                    this.props.farm.farm((result) => {
                        this.props.farmComplete(result);
                        this.setState({
                            isFarming: false
                        })
                    });
                })
        }
    }

    render() {
        return (
            <div className='farm column frame farm'>
                <div className='rpgui-container framed'>

                    <div className={"row"}>
                        <strong>Resources:</strong>
                        <hr/>
                    </div>

                    <div className='rpgui-container framed-grey resources'>

                        <div className="columns">
                            <div className="column">
                                Wood:
                            </div>
                            <div className="column rtl">
                                {this.props.resources[ResourceTypes.Wood]}
                            </div>
                        </div>

                        <div className="columns">
                            <div className="column">
                                Metal:
                            </div>
                            <div className="column rtl">
                                {this.props.resources[ResourceTypes.Metal]}
                            </div>
                        </div>

                        <div className="columns">
                            <div className="column">
                                Cloth:
                            </div>
                            <div className="column rtl">
                                {this.props.resources[ResourceTypes.Cloth]}
                            </div>
                        </div>

                        <div className="columns">
                            <div className="column">
                                Diamond:
                            </div>
                            <div className="column rtl">
                                {this.props.resources[ResourceTypes.Diamond]}
                            </div>
                        </div>

                    </div>

                    <div className={"row"}>
                        <button className='rpgui-button'
                                onClick={this.farm}
                                disabled={
                                    this.state.isFarming ||
                                    this.props.player.staCurrent < 1 ||
                                    this.props.player.dead
                                }>

                            <span className="icon">
                                <i className="fas fa-tree"/>
                            </span>

                            <span>
                                &nbsp;
                                {
                                    this.state.isFarming ?
                                        this.props.farm.timer.getTimeoutString() : "Farm!"
                                }
                            </span>

                        </button>
                    </div>

                </div>

            </div>
        )
    }
}