import Delay from "../delay";
import {Unknown} from "../data/types";

export default class Item {

    equiped = false;
    onDoneCreating = null;

    constructor({
                    category = Unknown,
                    name,
                    slot,
                    isMultiSlot = false,
                    level = 1,
                    material,
                    delay = global.delay || 10
                } = {}) {

        this.delay = new Delay(delay);

        this.delay.onDelayExpired = () => {
            if (this.onDoneCreating) {
                this.onDoneCreating();
            }
        };

        this.material = material;
        this.category = category;
        this.slot = slot;
        this.isMultiSlot = isMultiSlot;
        this.name = name;
        this.level = level;
    }
}
