import Timer from "../tools/timer";
import Resources from "./resources";

export default class Farm {

    constructor({
                    delay = global.delay
                } = {}) {
        this.delay = delay;
        this.timer = new Timer({
            delay,
            autoStart: false
        });
    }

    farm(
        callback
    ) {
        this.timer.callback = () => {

            this.timer.pause();
            this.timer = new Timer({
                delay: this.delay,
                autoStart: false
            });

            callback(
                new Resources({
                    initialResources: 1
                })
            );

        };

        this.timer.start();
    }
}